@font-face
    font-family: 'Murmure'
    src: url('fonts/Le_Murmure-Regular_web.woff') format('woff'), url('fonts/Le_Murmure-Regular_web.woff2') format('woff2')
    font-weight: normal
    font-style: normal

@font-face
    font-family: 'HKGrotesk'
    src: url('fonts/HKGrotesk-Regular.otf') format('opentype')
    font-weight: normal
    font-style: normal

@font-face
    font-family: 'HKGrotesk'
    src: url('fonts/HKGrotesk-Bold.otf') format('opentype')
    font-weight: bold
    font-style: normal

body
    background-color: blue
    padding-top: 10vh
    overflow-x: hidden

*
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

.background
    position: absolute
    top: 0
    z-index: -1
    mix-blend-mode: lighten
    img
        position: absolute
        max-width: 50vw

.container
    max-width: 620px
    width: 100%
    margin: 0 auto

p
    display: inline-block
    color: white
    font-size: 1.2rem
    font-family: 'HKGrotesk'
    font-weight: bold
    line-height: 1.5rem
    margin: 0.3rem 0
    @media screen and (max-width: 25rem)
        font-size: 1rem
        line-height: 1rem

p.quote
    font-family: 'Murmure'
    font-size: 3rem
    line-height: 3rem

br
    line-height: 1rem

h1,h2,h3,h4,h5,h6,li
    font-size: 3rem
    line-height: 6.5rem
    display: inline-block
    color: white
    font-family: 'Murmure'

h2
    font-size: 4rem
    line-height: 4rem
    margin-top: 6rem
    margin-bottom: 1rem

h1
    font-size: 7rem
    margin-bottom: 2rem

*::selection
    background-color: blue
    color: red

ul
    padding-left: 0
    margin-top: 0

li
    font-size: 1rem
    font-family: 'HKGrotesk'
    font-weight: bold
    text-decoration: none
    line-height: 1.5rem
    @media screen and (max-width: 25rem)
        font-size: 1rem
        line-height: 1rem

/* img
    filter: contrast(2) saturate(6) */

a
    color: yellow

.spacer
    background-color: red